
"use client";

import type { FocusEvent, MouseEvent as ReactMouseEvent } from 'react';
import { useState, useEffect, useRef } from 'react';
import type { CellData, CellAddress } from '@/types/spreadsheet';
import { Input } from "@/components/ui/input";
import { cn } from '@/lib/utils';
import { getCellId as getCellIdUtil } from '@/types/spreadsheet';

interface CellProps {
  cellData: CellData;
  address: CellAddress;
  onCellChange: (rowIndex: number, colIndex: number, rawValue: string | number) => void;
  isActive: boolean; // Is this the primary active cell in the grid (for styling, formula bar target)
  isInSelectionRange: boolean; // Is this cell part of the general multi-cell selection range
  isFormulaHighlightTarget: boolean; // Is this cell part of the formula construction highlight
  onSelect: (address: CellAddress, isShiftKey: boolean, isDrag: boolean) => void; 
  startEditing: (address: CellAddress) => void; 
  stopEditing: () => void;
  isEditingThisCell: boolean; // Is this specific cell currently in direct input edit mode
  width?: number;
  isActivelyEditingFormulaGlobal?: boolean; // Is the formula bar globally active for editing
}

export default function Cell({ 
  cellData, 
  address, 
  onCellChange, 
  isActive, 
  isInSelectionRange,
  isFormulaHighlightTarget,
  onSelect,
  startEditing,
  stopEditing,
  isEditingThisCell,
  width,
  isActivelyEditingFormulaGlobal 
}: CellProps) {
  const [editValue, setEditValue] = useState(cellData.rawValue || '');
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    // If this cell is not the one being edited, sync its display value from cellData.rawValue
    if (!isEditingThisCell) {
      setEditValue(cellData.rawValue?.toString() || '');
    }
  }, [cellData.rawValue, isEditingThisCell]);

  useEffect(() => {
    if (isEditingThisCell && inputRef.current) {
      inputRef.current.focus();
      // Move cursor to the end of the input value
      inputRef.current.selectionStart = inputRef.current.selectionEnd = inputRef.current.value.length;
    }
  }, [isEditingThisCell]);
  
  const handleMouseDown = (e: ReactMouseEvent<HTMLTableCellElement>) => {
    // If this cell is already being edited and the click is inside its input, do nothing.
    if (isEditingThisCell && inputRef.current && inputRef.current.contains(e.target as Node)) {
      return; 
    }
    // Otherwise, delegate to onSelect.
    // `isDrag` is false for mousedown, as it's the start of a potential drag.
    onSelect(address, e.shiftKey, false); 
  };
  
  const handleClick = (e: ReactMouseEvent<HTMLTableCellElement>) => {
    // If the formula bar is active, clicks on cells are for formula building (handled by onMouseDown/onSelect).
    // Prevent further actions like starting direct cell edit.
    if (isActivelyEditingFormulaGlobal) {
        e.preventDefault(); 
        return;
    }

    // If this cell is already being edited and click is on input, do nothing.
    if (isEditingThisCell && inputRef.current && inputRef.current.contains(e.target as Node)) {
        return;
    }

    // If the cell is active but not being edited directly, start editing.
    // (onMouseDown would have made it active if it wasn't already)
    if (isActive && !isEditingThisCell) {
        startEditing(address);
    } else if (!isActive && !isEditingThisCell) {
        // This case implies onMouseDown made it active, now this click should start editing.
        // However, onMouseDown already calls onSelect which might make it active.
        // For simplicity, a single click should make it active (via onSelect) and then editable.
        startEditing(address); 
    }
  };

  const handleDoubleClick = (e: ReactMouseEvent<HTMLTableCellElement>) => {
    if (isActivelyEditingFormulaGlobal) {
        e.preventDefault();
        return;
    }
    // Always start editing on double click if not already editing.
    if (!isEditingThisCell) {
        startEditing(address);
    }
  };

  const handleMouseEnter = (e: ReactMouseEvent<HTMLTableCellElement>) => {
    // If left mouse button is pressed (i.e., dragging)
    if (e.buttons === 1) { 
      // Pass true for isDrag parameter to onSelect for drag selection
      onSelect(address, false, true); 
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setEditValue(e.target.value);
    // If formula bar is active, and user types in cell, potentially update formula bar? (Complex interaction, defer)
    // For now, direct cell edit updates formula bar if it was targeting this cell.
    if (isActivelyEditingFormulaGlobal && isActive) {
        // This sync is tricky. Formula bar should be source of truth when it's active.
        // Perhaps disable direct cell edit entirely if formula bar is active?
        // For now, assume if user types in cell while formula bar is active for THIS cell, it's fine.
    }
  };

  const handleBlur = (e: FocusEvent<HTMLInputElement>) => {
    // Only commit change if the value actually changed from the original rawValue
    // Or if it's a formula (starts with '='), always commit to re-evaluate.
    if (String(editValue) !== String(cellData.rawValue) || String(editValue).startsWith('=')) { 
      onCellChange(address.rowIndex, address.colIndex, editValue);
    }
    stopEditing(); 
  };
  
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' || e.key === 'Tab') {
      e.preventDefault(); 
      inputRef.current?.blur(); // Triggers handleBlur to save and stop editing
      // TODO: Add navigation to next cell on Enter/Tab
    } else if (e.key === 'Escape') {
      e.preventDefault();
      setEditValue(cellData.rawValue?.toString() || ''); // Revert to original
      inputRef.current?.blur(); // Triggers stopEditing without saving changes via onCellChange
    }
  };

  const cellStyleFromData = cellData.style || {};
  const appliedStyle: React.CSSProperties = {
    fontWeight: cellStyleFromData.bold ? 'bold' : 'normal',
    fontStyle: cellStyleFromData.italic ? 'italic' : 'normal',
    textDecoration: cellStyleFromData.underline ? 'underline' : 'none',
    textAlign: cellStyleFromData.textAlign as React.CSSProperties['textAlign'],
    width: width ? `${width}px` : undefined,
    // Background color can be conditional here based on selection states
  };

  const cellClasses = cn(
    "p-1.5 border text-sm truncate min-w-[var(--default-cell-width,120px)] relative select-none outline-none",
    // Base styling
    {"font-bold": cellData.style?.bold},
    {"italic": cellData.style?.italic},
    {"underline": cellData.style?.underline},
    cellData.style?.textAlign === 'left' && "text-left",
    cellData.style?.textAlign === 'center' && "text-center",
    cellData.style?.textAlign === 'right' && "text-right",
    // Conditional styling for selection states
    // Normal selection range (not the primary active cell, and not formula editing)
    isInSelectionRange && !isActive && !isActivelyEditingFormulaGlobal && "bg-primary/20", 
    // Formula highlight target (has precedence over isInSelectionRange if both true)
    isFormulaHighlightTarget && "bg-green-500/30 border-green-700 border-dashed",
  );


  // If this cell is being directly edited (input field is active)
  if (isEditingThisCell && !isActivelyEditingFormulaGlobal) { 
    return (
      <td 
        className={cn(cellClasses, "p-0 z-10")} // Remove padding from td, apply to input
        style={{...appliedStyle, minWidth: width ? `${width}px` : '120px'}}
        // Mouse down on the td itself (not the input) should still behave like normal cell mousedown
        onMouseDown={(e) => { 
            // Prevent mousedown on td from interfering if target is already the input
            if (inputRef.current && !inputRef.current.contains(e.target as Node)) {
                handleMouseDown(e);
            }
        }}
        onMouseEnter={handleMouseEnter} // Allow drag selection over an editing cell
      >
        {/* Active cell border shown when this cell is the primary active one AND not in global formula editing mode */}
        {isActive && !isActivelyEditingFormulaGlobal && (
            <div className="absolute inset-[-1px] border-2 border-primary pointer-events-none rounded-[1px] z-20" />
        )}
        <Input
          ref={inputRef}
          type="text"
          value={editValue}
          onChange={handleChange}
          onBlur={handleBlur}
          onKeyDown={handleKeyDown}
          className="w-full h-full p-1.5 box-border text-sm outline-none rounded-none border-transparent focus:ring-0 focus:border-transparent bg-background"
          style={{ 
            fontWeight: appliedStyle.fontWeight,
            fontStyle: appliedStyle.fontStyle,
            textDecoration: appliedStyle.textDecoration,
            textAlign: appliedStyle.textAlign,
          }}
          aria-label={`Edit cell ${getCellIdUtil(address.rowIndex, address.colIndex)}`}
        />
      </td>
    );
  }

  // Default display mode for the cell
  return (
    <td
      className={cellClasses}
      style={{...appliedStyle, minWidth: width ? `${width}px` : '120px'}}
      onClick={handleClick}
      onDoubleClick={handleDoubleClick}
      onMouseDown={handleMouseDown}
      onMouseEnter={handleMouseEnter}
      tabIndex={0} 
      aria-readonly={true} 
      data-cell-id={getCellIdUtil(address.rowIndex, address.colIndex)}
    >
      {/* Active cell border shown when this cell is the primary active one AND not in global formula editing mode */}
      {isActive && !isActivelyEditingFormulaGlobal && (
          <div className="absolute inset-[-1px] border-2 border-primary pointer-events-none rounded-[1px] z-20" />
      )}
      {/* Special highlight for formula target, if not the primary active cell (primary active gets its own border) */}
      {isFormulaHighlightTarget && !isActive && (
         <div className="absolute inset-[-1px] border border-dashed border-green-700 pointer-events-none rounded-[1px] z-10" />
      )}
      {String(cellData.value ?? '')}
    </td>
  );
}
